// {"title":"Fences","description":"How to apply markdown fences correctly.","audience":"Everyone","level":"Beginner","section":"Markdown","link":"/basics/02-fences","source":"/home/runner/work/pidoc/pidoc/packages/example-app/docs/basics/02-fences.md"}
import * as React from 'react';
import { ContentPage } from '@pidoc/components';

    import { PageContent, Markdown, PageLayout } from '@pidoc/components';

    const link = "https://github.com/smapiot/pidoc/tree/develop/packages/example-app/docs/basics/02-fences.md";
    const html = `
<h1 id="markdown-fences">Markdown Fences</h1>
<p>Every documentation has special needs. For bringing up special remarks and comments <em>fences</em> exist. They are always enclosed in <code>:::</code>.</p>
<h2 id="types-of-fences">Types of Fences</h2>
<h3 id="tips">Tips</h3>
<p>Tips look as below.</p>
<div class="box tip"><p class="box-title">Title</p>
<p>This is the tip itself.</p>
</div>
<p>The code for the section above looks like this:</p>
<pre><code class="hljs language-md">::: tip: Title
This is the tip itself.
:::
</code></pre>
<h3 id="warnings">Warnings</h3>
<p>Warnings look as below.</p>
<div class="box warning"><p class="box-title">Title</p>
<p>This is the warning itself.</p>
</div>
<p>The code for the section above looks like this:</p>
<pre><code class="hljs language-md">::: warning: Title
This is the warning itself.
:::
</code></pre>
<h3 id="failures">Failures</h3>
<p>Failures look as below.</p>
<div class="box failure"><p class="box-title">Title</p>
<p>This is the failure itself.</p>
</div>
<p>The code for the section above looks like this:</p>
<pre><code class="hljs language-md">::: failure: Title
This is the failure itself.
:::
</code></pre>
<h3 id="successes">Successes</h3>
<p>Successes look as below.</p>
<div class="box success"><p class="box-title">Title</p>
<p>This is the success itself.</p>
</div>
<p>The code for the section above looks like this:</p>
<pre><code class="hljs language-md">::: success: Title
This is the success itself.
:::
</code></pre>
<h3 id="questions">Questions</h3>
<p>Questions look as below.</p>
<div class="box question"><p class="box-title">Title</p>
<p>This is the question itself.</p>
</div>
<p>The code for the section above looks like this:</p>
<pre><code class="hljs language-md">::: question: Title
This is the question itself.
:::
</code></pre>
<h3 id="summaries">Summaries</h3>
<p>Summaries look as below. In contrast to other fences they are collapsible.</p>
<details class="summary"><summary>Title</summary>
<p>This is the summary itself.</p>
</details>
<p>The code for the section above looks like this:</p>
<pre><code class="hljs language-md">::: summary: Title
This is the summary itself.
:::
</code></pre>
<div class="Generic">
<p>Besides the given fences you are also able to use generic fences, i.e., just some <code>&lt;div&gt;</code>.</p>
<p>Take for instance the following code:</p>
<pre><code class="hljs language-md">::: #warning
<span class="hljs-emphasis">*here be dragons*</span>
:::
</code></pre>
<p>this results in</p>
<pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">id</span>=<span class="hljs-string">&quot;warning&quot;</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">em</span>&gt;</span>here be dragons<span class="hljs-tag">&lt;/<span class="hljs-name">em</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
<p>As seen here:</p>
<div id="warning">
<p><em>here be dragons</em></p>
</div>
<p>More details on <a href="https://www.npmjs.com/package/markdown-it-div">generic fences here</a>.</p>
</div>
`;
    const meta = {"title":"Fences","description":"How to apply markdown fences correctly.","audience":"Everyone","level":"Beginner","section":"Markdown"};
  

export default () => (
  <ContentPage>
    
    <PageLayout name={"default"} meta={meta}>
      <PageContent meta={meta}>
        <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
      </PageContent>
    </PageLayout>
  
  </ContentPage>
);